import { FirestoreProvider } from './../../providers/firestore/firestore';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { OtuAuthProvider } from '../../providers/otu-auth/otu-auth';

/**
 * Generated class for the ApprovalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-approval',
  templateUrl: 'approval.html',
})
export class ApprovalPage {

  appliedUsers;
  email;
  user;
  msg;

  constructor(
                public navCtrl: NavController
              , public navParams: NavParams
              , public auth: OtuAuthProvider
              , public firestore: FirestoreProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ApprovalPage');
    this.auth.getUser().subscribe((u) => {
      if(u != undefined || u != null) {
        this.email = u.email;
        this.firestore.getAppliedUser(u.email).valueChanges().subscribe((a) => {
          console.log(u.email);
          this.appliedUsers = a;
          console.log(a);
          if(a != undefined && a.length > 0){
            a.forEach((au) => {
                this.firestore.getRole(au['appliedFor']).valueChanges().subscribe((r) => {
                  if(r != undefined && r.length > 0) {
                    au['canAdd'] = r[0]['canAdd'];
                  }
                })
            })
          } else {
            this.msg = "No applied users available";
          }
        })
      }
    });
  }
  submit() {
    console.log(`The is is ${this.appliedUsers[0].$id}`);
    this.firestore.updateApprovedUsers(this.appliedUsers).then(() => {
      alert("Success");
      this
    }, (e) => alert("Failed"))
  }

}



